import graph1 from "../images/graph1.png"
import graph2 from "../images/graph2.png"
import tabulka1 from "../images/tabulka1.png"
import tabulka2 from "../images/tabulka2.png"
import {Row,Col} from 'react-bootstrap'
/**
 * Render page about automaton's document
 * @param {*} props 
 * @returns <p> tag
 */
const Documents=(props)=>{
    return(
        <p style={{paddingLeft: "10em"}}>
            <h2>1.1 Jazyk a jeho konen reprezentace</h2>
            <b>1.1.1 Abeceda</b>
            <br/>&emsp;- Abeceda je libovoln konen mnoina prvk, kter nazvme symboly (nebo psmena i znaky). Abecedu budeme znait  (eck psmeno velk sigma).
            <br/>pklad: abeceda  ,kter m dva symboly 0 a 1.
            <br/><b>1.1.2 Slovo </b>
            <br/>&emsp;- Slovo nad abecedou , strunji jen slovo, je libovoln konen posloupnost prvk mnoiny , kterou budeme zapisovat bez rek mezi jejmi prvky. Mnoinu vech slov nad abecedou  oznaujeme vrazem . Dlka slova je poet prvk v tomto slov. Dlku slova u oznaujeme |u|. Slovo o dlce 0 nazvme przdn slovo a zname  (eck psmeno epsilon).
            <br/>pklad: slovo u = 00101001.
            <br/><b>1.1.3 Jazyk</b>
            <br/>- Jazyk nad abecedou  definujeme jako libovolnou mnoinu slov nad abecedou , tedy jako libovolnou podmoinu mnoiny vech slov  nad abecedou . Jazyk zname L
            <br/>pklad: Jazyk L kter m prvky ,0,1,10,11 a 00.
            <br/>
            <h2>1.2 Konen automat</h2>
            <b>1.2.1 Deterministick:</b>
            <br/>&emsp;- Deterministick konen  automat se definuje jako uspodan ptice A = (Q,,,q0,F), kde Q je konen neprzdn mnoina stav,  je konen neprzdn mnoina vstupnch symbol (abeceda), : Q x  Q pechodov funkce, q0 Q je poten stav a F Q je mnoina kocovch stav a plat. 
            <br/><b>1.2.2 Nedeterministick:</b>
            <br/>&emsp;- Nedeterministick konen automat je takov konen automat A = (Q, , , q0, F), kde pechodov funkce  : Q    P(Q), piem ostatn sousti definice jsou stejn jako u (deterministickho) konenho automatu.
            <br/><b>1.2.3 Reprezentace automatu:</b>
            <br/>&emsp;- Stavovm diagramem(grafem):
            <Row>
                <Col>
                    <cite> Deterministick konen  automat</cite>
                    <img  width="450" height="200" src={graph1} alt="graf deter.kon.automatu"/>
                </Col>
                <Col>
                    <cite> Nedeterministick konen  automat</cite>
                    <img  width="450" height="200" src={graph2} alt="graf nedeter.kon.automatu"/>
                </Col>
            </Row>
            <br/>&emsp;- Pechodovou tabulkou:
            <Row>
                <Col>
                    <img width="650" height="200" src={tabulka1} alt="tabulka deter.kon.automatu"/>
                </Col>
                <Col> 
                    <img  width="650" height="200" src={tabulka2} alt="tabulka nedeter.kon.automatu"/>
                </Col>
            </Row>
            <br/>&emsp;- -funkc. (vypnuto, zapni) = zapnuto
            <br/><b>1.2.4 Reprezentace automatu:</b>
            <br/>&emsp;- Nech je dn konen automat A = (Q, , , q0, F). ekneme, e slovo w   je pijmno (rozpoznvno) automatem A, jestlie q0 w F, tedy pokud automat A pejde z potenho stavu q0 petenm slova w do nkterho koncovho stavu z mnoiny F.
        </p>
    );
}
export default Documents