/**
 * Render application's instruction and structure
 * @param {*} props 
 * @returns <p> tag
 */
const Home=(props)=>{
    return(
        <p style={{paddingLeft: "10em"}}>  
            <h2>Zadan projektu</h2>
            &emsp;S vyuitm knihovny ReactJS vytvote komponentu pro zobrazen a minimalistickou editaci orientovanho grafu, tj. entity s pojmenovanmi vrcholy (odchoz a pchoz hrany) a hranami (poten a koncov vrchol). Pro vizualizaci vyuijte SVG. Potebn data pevezmte z props.
            <h2>Abstrakt</h2>
            &emsp;Clem webov aplikace: Webov editor pro konen automat, kter slou pro podporu vuky. Aplikace mus umonit uivateli 
            zadat konen automat run nebo souboru. Zadan automat musm pehledn vykreslit.
            <br/>&emsp;<b>Klov slova:</b> Abeceda, slovo, konen automat, SVG, React

            <h2>Pouit technologie</h2>
            &emsp;React, SVG(Scalable Vector Graphics), ostatni knihovny.

            <h2>Pouit funkce</h2>
            1 Zadn konen automat
            <br/>&emsp;1.1 Run(Tvova automatu)
            <br/>&emsp;1.2 Naten ze souboru(Import)   
            <br/>&emsp;1.3 Vbr z pklad(Template page)  
            <br/>2.0 Uprava
            <br/>3.0 Slovo je prijmano automatem
            <br/>4.0 Pechodov tabulka
            <br/>5.0 Download

            <h2>Struktura strnky Automat a jeho hlavn komponenty</h2>
            1. Handle: 
            <br/>&emsp;- Komponenta je zodpovdn za poskytovn promnnch vrchol, hran, symbol a funkc pro jejich pravu, pidn nebo odstrann. Pokld se z komponent HandlePoints, HandleEdges a HandleSymbols.
            <br/>2. Graph:
            <br/>&emsp;- Komponenta pedstavuje automat ve formtu SVG. Pokld se z komponent Points,Edges a GrafInf.
            <br/>3. WordTest:
            <br/>&emsp;- Komponenta uruje, zda slovo je pijmno automatem.
            <br/>4. StateTransitionTable:
            <br/>&emsp;- komponenta je zodpovdn za reprezentaci pechodov tabulky automatu.
            <br/>5. Import a Download:
            <br/>&emsp;5.1 Import: Pouvj se element input, object FileReader, method readAsText() a method onLoad(). Clem je naten json soubor jako initial data.
            <br/>&emsp;5.2 Download: Pouv se function saveAs od knihovny file-saver. Clem je uloen svg element jako html soubor.
        </p>
    );
}
export default Home